/**
 * Optimizes Google Play icon URLs by appending/modifying the size parameter.
 * Google's CDN supports parameters like =s128 or =s64 to resize images on the fly.
 * @param {string} url The original icon URL
 * @param {number} size The desired size in pixels
 * @returns {string} Optimized URL
 */
export function optimizeIcon(url, size = 128) {
    if (!url) return url;

    // Remove existing size parameters or format modifiers
    // Typically URLs end with =s... or -rw
    let optimized = url.split('=')[0]; // Remove existing parameters

    // Append new size and force high quality/format
    return `${optimized}=s${size}-rw`;
}
